<template>
  <div class="container"> 
    <div class="infoMoudel" ref="infoMoudel">
        <UserInfo></UserInfo>
    </div>
  </div>
</template>
<script>
import UserInfo from './compoments/userInfo'
export default {
  name:'login',
  components:{UserInfo},
  data(){
    return{
    }
  },
  mounted() {
     this.resiZie()
     window.onresize = () => {
            return (() => {
              this.resiZie()
            })()
        }
  },
  methods: {
    resiZie(){
      const headerHeight = this.$root.$children[0].$children[0].$refs.header.$el.offsetHeight;
      const fooer = this.$root.$children[0].$children[0].$refs.footer.$el.offsetHeight;
      this.$refs.infoMoudel.style.height =(window.innerHeight - headerHeight - fooer) +'px';
    },
  },
}
</script>
<style lang="less" >
  .infoMoudel{
    width: 100%;
    position: relative;
    background: url('../assets/img/login_bg.png') repeat-y;
    background-size: 100% 100%;
    .loginForm{
    }
  }
</style>


