<template>
  <div class="userInfo">
      <div class="stepOne" v-show="step === '1'">
      <p class="title">完善您的基本信息</p>
        <el-form :model="ruleOneForm" :rules="rulesOne" ref="ruleOneForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="头像:" required >
            <el-upload
              class="avatar-uploader"
              action="https://jsonplaceholder.typicode.com/posts/"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload">
              <img v-if="imageUrl" :src="imageUrl" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
            <p class="uploadText">建议使用招聘者真实头像提升真实性、专业性支持jpg、jpeg、gif、png，小于10MB</p>
          </el-form-item>
          <el-form-item label="姓名:" prop="name">
              <el-input v-model="ruleOneForm.name"></el-input>
          </el-form-item>
          <el-form-item label="性别:" prop="sex">
            <el-radio label="男" v-model="ruleOneForm.sex"></el-radio>
            <el-radio label="女" v-model="ruleOneForm.sex"></el-radio>
          </el-form-item>
          <el-form-item label="求职身份:" prop="type">
            <el-select v-model="ruleOneForm.type" placeholder="请选择身份">
              <el-option label="职场人-有正式工作经历" value="1"></el-option>
              <el-option label="学生-在校/应届/往届" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="出生年月:" prop="birthday">
              <el-date-picker type="date" placeholder="选择日期" v-model="ruleOneForm.birthday" style="width: 100%;"></el-date-picker>
          </el-form-item>
          <el-form-item label="参加工作时间:" prop="jobDate">
              <el-date-picker type="date" placeholder="选择日期" v-model="ruleOneForm.jobDate" style="width: 100%;"></el-date-picker>
          </el-form-item>
          <el-form-item label="当前求职状态:" prop="status">
            <el-select v-model="ruleOneForm.status" placeholder="请选择到岗时间">
              <el-option label="在校-考虑机会" value="1"></el-option>
              <el-option label="离校-随时到岗" value="2"></el-option>
              <el-option label="在校-暂不考虑" value="3"></el-option>
              <el-option label="在校-考虑机会" value="4"></el-option>
              <el-option label="在校-月内到岗" value="5"></el-option>
            </el-select>
          </el-form-item>
            <div class="nextBtn">
              <button type="button"  @click="submitForm('ruleOneForm')">下一步</button>  
            </div>  
        </el-form>
      </div>
      <div class="stepTwo" v-show="step === '2'">
        <p class="title">您的学历信息</p>
          <el-form :model="ruleTwoForm" :rules="rulesTwo" ref="ruleTwoForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="学校名称" prop="schoolName">
              <el-input v-model="ruleTwoForm.schoolName" placeholder="例如：北京大学"></el-input>
            </el-form-item>
            <el-form-item label="学历"  prop="edu">
              <el-select v-model="ruleTwoForm.edu" placeholder="请选择到岗时间">
                <el-option label="在校-考虑机会" value="1"></el-option>
                <el-option label="离校-随时到岗" value="2"></el-option>
                <el-option label="在校-暂不考虑" value="3"></el-option>
                <el-option label="在校-考虑机会" value="4"></el-option>
                <el-option label="在校-月内到岗" value="5"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="所学专业"  prop="kill">
              <el-input v-model="ruleTwoForm.kill" placeholder="例如：计算机科学与技术"></el-input>
            </el-form-item>
            <el-form-item label="入学时间"  prop="endTme">
              <el-date-picker type="date" placeholder="选择日期" v-model="ruleTwoForm.endTme" style="width: 100%;"></el-date-picker>
            </el-form-item>
            <el-form-item class="" style="text-align:left">
              <el-checkbox label="是否实习" name="type" v-model="ruleTwoForm.placement"></el-checkbox>
            </el-form-item>
            <div class="prevBtn">
              <button type="button" @click="changeStep('1')">上一步</button>  
            </div>  
            <div class="nextBtn">
              <button type="button"  @click="submitForm('ruleTwoForm')">下一步</button>  
            </div>  
          </el-form>
      </div>
      <div class="stepThree" v-show="step === '3'">
        <p class="title">您的学历信息</p>
        <el-form label-position="top" :model="ruleThreeForm" :rules="rulesThree" ref="ruleThreeForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="期望的职位" prop="job">
            <el-input v-model="ruleThreeForm.job" placeholder="填写具体的岗位名称"></el-input>
          </el-form-item>
          <el-form-item label="期望工作的地点" prop="address">
            <el-input v-model="ruleThreeForm.address" placeholder="填写城市名称，例如：兰州"></el-input>
          </el-form-item>
          <el-form-item label="个人优势" prop="personalKills">
            <el-input  type="textarea" resize="none" show-word-limit="true" :autosize="{ minRows: 3, maxRows: 3 }" v-model="ruleThreeForm.personalKills" placeholder="您可以从性格、技能、亮点等方面介绍自己,最多140字"></el-input>
          </el-form-item>
        </el-form>
        <div class="prevBtn">
          <button type="button" @click="changeStep('2')">上一步</button>  
        </div>  
        <div class="nextBtn">
          <button type="button"  @click="submitForm('ruleTwoForm')">下一步</button>  
        </div>  

      </div>
  </div> 
</template>
<script>
export default {
  data(){
    return{
        step:'1',
        imageUrl: '',
        ruleOneForm: {
          name: '',
          sex:'',
          type:'',
          birthday:'',
          jobDate:"",
          status:'1'
        },
        ruleThreeForm: {
          schoolName: '',
          edu:'',
          kill:'',
          endTme:'',
          placement:false
        },
        ruleTwoForm: {
          job: '',
          address:'',
          personalKills:'',
        },
        rulesOne: {
          name: [
            { required: true, message: '请输入姓名', trigger: 'blur' },
          ],
          sex: [
            { required: true, message: '请选择性别', trigger: 'change' },
          ],
          type: [
            { required: true, message: '请选择身份', trigger: 'change' },
          ],
          birthday: [
            { required: true, message: '请选择出生年月', trigger: 'blur' },
          ],
          jobDate: [
            { required: true, message: '请选择参加工作时间', trigger: 'blur' },
          ],
          status: [
            { required: true, message: '请选择求职状态', trigger: 'blur' },
          ]
        },
        rulesTwo:{
          schoolName: [
            { required: true, message: '请输入学校名称', trigger: 'blur' },
          ],
          edu:[
            { required: true, message: '请选择学历', trigger: 'change' },
          ],
          kill:[
            { required: true, message: '请输入所学专业', trigger: 'blur' },
          ],
          endTme:[
            { required: true, message: '请选择入学时间', trigger: 'change' },
          ],

        },
        rulesThree:{
          job: [
            { required: true, message: '请输入期望的职业', trigger: 'blur' },
          ],
          address:[
            { required: true, message: '请输入地址', trigger: 'blur' },
          ],
          personalKills:[
            { required: true, message: '请输入个人介绍', trigger: 'blur' },
          ]
        }
    }
  },
  methods: {
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 10;
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 10MB!');
      }
      return isLt2M;
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        console.log(this.ruleForm)
        if (valid) {
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    changeStep(step){
      this.step = step
    }
  }

}
</script>
<style lang="less" >
  .userInfo{
    width: 350px;
    padding: 10px  20px 10px 10px;
    height: 492px;
    position: absolute;
    left:0;right: 0;bottom: 0; top: 0;
    margin: auto;
    background-color: #fff;
    border-radius: 5px;    
    font-size: 13px;
    letter-spacing: normal;
    color: #333333;
    .title{
      text-align:left;
      font-size: 16px;
      border-bottom: 1px solid #333;
      padding-bottom: 10px;
      margin-bottom: 10px;
    }
    .stepOne{
      .el-input__inner{width: 220px;}
      .el-form-item{
        margin-bottom: 18px;
        text-align: left;
      }
      .el-form-item__content{
        margin-left: 120px !important;
      }
      .el-form-item__label{
        width: 120px !important;
      }
      .avatar-uploader{
        width: 35px;
        text-align: center;
        height: 35px;
        border:1px solid #999;
        border-radius: 50%;
        overflow: hidden;
        display: inline-block;
        width: 35px;
        float: left;
        .el-icon-plus{
          vertical-align: 2px;
        }
        .avatar{
          width: 35px;
          height: 35px;
        }
      }
      .uploadText{
        float: left;
        display: inline-block;
        font-size: 12px;
        color: #999;
        width: 165px;
        padding:0 5px;
        line-height: 12px;
        text-align: left;
        vertical-align: top;
      }
    }
    .stepTwo{
      .el-input__inner{width: 220px;}
      .el-form-item{
        margin-bottom: 18px;
        text-align: left;
      }
      .el-form-item__content{
        margin-left: 120px !important;
      }
      .el-form-item__label{
        width: 120px !important;
      }
    }
    .stepThree{
      .el-input__inner{width: 100%;}
      .el-form-item{
        margin-bottom: 18px;
        text-align: left;
      }
    }
    
      
  .nextBtn{
    padding: 0 10px 0 10px;
    button{
      display: inline-block;
      background-color: #10bbb8;
      outline: none;
      color: #fff;
      width: 100%;
      padding: 10px 0;
      border: 0 none;
      border-radius: 3px;
      &:hover{
        cursor: pointer;
      }
    }
  }
  .prevBtn{
    margin-bottom: 10px;
    padding: 0 10px 0 10px;
    button{
      display: inline-block;
      background-color: #fff;
      outline: none;
      color: #10bbb8;
      width: 100%;
      padding: 10px 0;
      border: 1px solid #10bbb8;
      border-radius: 3px;
      &:hover{
        cursor: pointer;
      }
    }
  }
  }
</style>


